.page {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

body {
  background-color: white;
  display: flex;
  justify-content: center;
}

.main__title {
  text-align: center;
}

.form__number-cards {
  text-align: center;
}

.card__container {
  display: flex;
  background-color: white;
  height: 200px;
  justify-content: center;
  margin-top: 20px;
  list-style: none;
  flex-wrap: wrap;
}

.card {
  width: 160px;
  background-color: #fff;
  margin: 0 10px;
  display: flex;
  justify-content: center;
  align-items: center;
  border: 2px solid black;
}
